body {
  font-family: "Roboto", Helvetica, sans-serif;
  font-weight: 400;
  background: radial-gradient(ellipse at center, white 0%, #ededed 100%);
  color: #333;
  font-size: 1.125em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  color: #49c5b6;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  color: #333;
  display: inline-block;
}

a:hover, a:focus {
  text-decoration: none;
}

button,
[type='button'],
.button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid #49c5b6;
  border-radius: 3px;
  padding: 5px 10px;
  color: #fff;
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  cursor: pointer;
  text-decoration: none;
  background: #49c5b6;
}

button:hover, button:focus,
[type='button']:hover,
[type='button']:focus,
.button:hover,
.button:focus {
  background: transparent;
  color: #49c5b6;
}

button + button,
button + [type='button'],
[type='button'] + button,
[type='button'] + [type='button'],
.button + button,
.button + [type='button'] {
  margin-left: 15px;
}

img {
  max-width: 100%;
  height: auto;
  width: auto;
}

form input,
form label {
  clear: both;
  display: block;
}

form input {
  margin-bottom: 1.5em;
}

dl,
dd,
dt {
  margin: 0;
}

form {
  width: 100%;
}

@media (min-width: 1025px) {
  form {
    max-width: 50%;
    margin: 0 auto;
  }
}

input[type='text'], input[type='number'], input[type='email'], input[type='date'], input[type='password'],
textarea[type='text'],
textarea[type='number'],
textarea[type='email'],
textarea[type='date'],
textarea[type='password'] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid #ddd;
  border-bottom-right-radius: 10px;
  padding: 5px 10px;
  min-height: 40px;
  -webkit-box-shadow: 3px 3px 10px 0 rgba(0, 0, 0, 0.25);
          box-shadow: 3px 3px 10px 0 rgba(0, 0, 0, 0.25);
  margin-top: 0.5em;
  margin-bottom: 1em;
  width: 100%;
}

input[type='text']:focus, input[type='number']:focus, input[type='email']:focus, input[type='date']:focus, input[type='password']:focus,
textarea[type='text']:focus,
textarea[type='number']:focus,
textarea[type='email']:focus,
textarea[type='date']:focus,
textarea[type='password']:focus {
  border-color: #49c5b6;
  outline: 0;
}

textarea {
  resize: vertical;
}

.loader {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 10;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  top: 0;
  left: 0;
  background: radial-gradient(ellipse at center, white 0%, #ededed 100%);
  opacity: 1;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
}

.loader.hidden {
  z-index: -1;
  opacity: 0;
  visibility: hidden;
}

.container {
  max-width: 994px;
  margin: 0 auto;
}

@media (max-width: 1023px) {
  .container {
    padding: 0 15px;
  }
}

.link__back {
  display: block;
  clear: both;
  margin-top: 25px;
  float: left;
}

.profiles__view-switch {
  clear: both;
  width: 100%;
  overflow: hidden;
  margin-bottom: 10px;
}

.view-switch__icon {
  float: right;
  margin-left: 10px;
}

.view-switch__icon svg {
  display: block;
  width: 32px;
  height: 32px;
  fill: #49c5b6;
}

.view-switch__icon:hover svg, .view-switch__icon:focus svg {
  fill: #70d1c6;
}

.profiles__list {
  margin-bottom: 1.5em;
}

.profiles-list__item {
  background: #fff;
  border: 1px solid #ddd;
  position: relative;
  cursor: pointer;
  margin-bottom: 1.5em;
  border-bottom-right-radius: 15px;
  -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.15);
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
}

.profiles-list__item:hover, .profiles-list__item:focus {
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
  background: radial-gradient(ellipse at center, white 0%, #ededed 100%);
}

.profiles-list__item:hover .profiles-list__preview, .profiles-list__item:focus .profiles-list__preview {
  color: #49c5b6;
}

@media (max-width: 319px) {
  .profiles-list__item {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    padding: 10px;
  }
}

@media (min-width: 320px) {
  .profiles-list__item {
    padding: 20px 40px;
  }
}

.profiles-list__item:last-of-type {
  border-bottom-width: 1px;
}

.profiles-list__preview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-decoration: none;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
}

.profiles-list__img {
  max-height: 150px;
  float: right;
}

.profiles-list__name {
  font-weight: 600;
  margin-bottom: 1.5em;
}

.profiles-list__info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  text-align: center;
}

@media (max-width: 319px) {
  .profiles-list__name {
    margin-bottom: 15px;
  }
}

.profiles__list--grid {
  display: grid;
  grid-gap: 30px;
}

@media (max-width: 767px) {
  .profiles__list--grid {
    grid-template-columns: repeat(1, 100%);
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .profiles__list--grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .profiles__list--grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.profiles__list--grid .profiles-list__item {
  margin: 0;
  padding: 20px;
}

.profiles__list--grid .profiles-list__item .profiles-list__preview {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.profiles__list--grid .profiles-list__item .profiles-list__preview .profiles-list__name {
  margin: 1.5em 0 0;
}

@media (min-width: 768px) {
  .profile {
    margin-left: -15px;
    margin-right: -15px;
  }
}

.profile__wrapper {
  background: #fff;
  border: 1px solid #eee;
  border-bottom-right-radius: 25px;
  -webkit-box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);
  overflow: hidden;
  padding: 15px;
}

@media (max-width: 767px) {
  .profile__remove-wrapper {
    overflow: hidden;
  }
}

.profile__remove-btn {
  float: right;
}

.profile__image,
.profile__info {
  width: 100%;
}

@media (min-width: 768px) {
  .profile__image,
  .profile__info {
    margin-right: 15px;
    margin-left: 15px;
    float: left;
    width: calc(50% - 30px);
  }
}

@media (max-width: 767px) {
  .profile__image {
    margin-bottom: 25px;
  }
}

.profile__img {
  display: block;
  margin: 0 auto;
}

.profile__attr-title {
  font-weight: 600;
  font-size: 1.25em;
}

.profile__attr {
  margin: 0.5em 0 1em;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: visibility ease 0.25s, opacity ease 0.25s;
  transition: visibility ease 0.25s, opacity ease 0.25s;
}

.modal.open {
  opacity: 1;
  visibility: visible;
}

.modal.hide-all:before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  content: '';
  background: #333;
  z-index: -1;
}

.modal button {
  background: #fff;
  color: #49c5b6;
  border-color: #fff;
}

.modal button:hover, .modal button:focus {
  color: #fff;
}

.modal a {
  color: #333;
}

.modal__wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3D(-50%, -50%, 1px);
          transform: translate3D(-50%, -50%, 1px);
  background-color: #49c5b6;
  color: #fff;
  padding: 20px;
  width: 497px;
  max-width: 100%;
  border-bottom-right-radius: 10px;
  -webkit-box-shadow: 0 1px 100px 0 rgba(0, 0, 0, 0.5);
          box-shadow: 0 1px 100px 0 rgba(0, 0, 0, 0.5);
}

.modal__close-btn {
  font-size: 3em;
  position: absolute;
  right: 10px;
  top: 0;
  color: #fff;
  cursor: pointer;
  -webkit-transition: -webkit-transform 0.25s;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  line-height: 1;
}

.modal__close-btn:hover, .modal__close-btn:focus {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

.hidden {
  display: none;
}
